body {
    margin: 0;
}

.dark_theme{
    background: rgb(159, 98, 34);
background: linear-gradient(354deg, rgb(56, 27, 1) 0%, rgba(40, 35, 33, 0.81) 100%);
    }
.dark_theme_title{
    color: #fff;
}

.main {
    padding: 0 75px;

    @include tablet {
        padding: 0 15px;
    }
}

#search_error {
    color: $color-warning-accent;
    font-family: $main-font;
    font-style: normal;
    font-weight: 300;
    font-size: 14px;
    text-align: center;
}

.pagination_container {
    // height: 70px;
    // overflow: hidden;
    margin-top: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    #page_numbers {
        display: flex;
    }
    .page-item {
        display: flex;
        align-items: center;
        justify-content: center;
    }
    .page-link {
        display: flex;
        align-items: center;
        justify-content: center;
        color: #000;
        border-radius: 5px;
        &:hover {
            color: #ff6b01;
        }
        // &:not(#prev-page, #next-page):focus {
        //     transition: ease all .2s;
        //     background-color: $color-accent !important;
        //     color: #fff !important;
        //     box-shadow: 0 0 0 0.2rem rgba(78, 33, 0, 0.315);
        // }
    }
    #prev-page, #next-page {
        &:focus {
            box-shadow: 0 0 0 0.2rem rgba(78, 33, 0, 0.315);
        }
    }
}
.active_page {
    transition: ease all .2s;
    background-color: $color-accent !important;
    color: #fff !important;
    box-shadow: 0 0 0 0.2rem rgba(78, 33, 0, 0.315);
}

.collection {
    position: relative;
    height: 100%;
    padding-left: 0;
    margin-top: 40px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

    @include rwd(768) {
        justify-content: space-evenly;
    }

    @include rwd(636) {
        margin-top: 20px;
    }

    li {
        list-style: none;
    }
}

.film-card {
    width: 275px;
    margin: 15px 10px;
    font-family: $main-font;
    transition: transform 0.5s;
    overflow: hidden;
    &:hover{
        transform: scale(1.05);
        box-shadow: -5px 0px 10px 1px rgba(107, 8, 8, 0.35);
    }
    @include rwd(636) {
        margin: 15px auto;
        width: 95%;
        height: 100%;
    }
    position: relative;

    // .film-card__img {
    //     background-image: url('../images/prev.jpg');
    //   }

    .film-card__img_container {
        position: relative;
        width: 100%;
        cursor: pointer;

        .film-card__img {
            width: 100%;
            height: 398px;
            border-radius: 5px;
            // background-image: url('./images/prev.jpg');
            // background-color: #fff;
// background-size: contain;
// background-repeat:no-repeat;
// background-origin: initial;
// background-size: 10cm;
// height: 100%;

            @include rwd(636) {
                height: 100%;
                min-height: 398px;
            }
        }
        .film-card__img::before {
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background-image: url('./images/prev.jpg');
            background-size:8cm;
            background-position:center;
            @include rwd(636) {
                background-size:cover;
            }
          }
        .film-card__vote {
            display: block;
            opacity: 1;
            position:absolute;
            text-align: center;
            left: 0;
            bottom: 0;
            background-color: #ff6b01;
            color:#fff;
            border-radius: 5px;
            padding: 0 6px;
           width:40px;
            &:hover{
                opacity: 0;
            }
        }
        .film-card__vote-count{
            display: block;
            opacity: 0;
            position:absolute;
            left: 0;
            bottom: 0;
            color:#ff6b01;
            background-color: #fff;
            border-radius: 5px;
            padding: 0 6px;
            width:40px;
            
            &:hover{
                opacity: 1;
                width: 50%;
            }
        }  
    }

    .film-card__title {
        font-style: normal;
        font-weight: 700;
        font-size: 14px;
        text-transform: uppercase;
        margin: 10px 0;
        margin-bottom: 5px;
        white-space: nowrap;
        overflow: hidden;
        padding-right: 3px;
        text-overflow: ellipsis;
    }

    .film-card__text {
        color: $btn-bg-accent;
        margin: 0;

        span {
            font-size: 14px;
        }
    }
}
.sort-by-genre{
    position: relative;
    // top: 10px;
    // left: 50%;
    // transform: translate(-50%);
    display: inline-block;
    z-index: 5;
}
.genres-header{
font-size: 20px;
color: orangered;
}
.apply-filter{
    background-color: #ff6b01;
    box-shadow: -5px 10px 10px 5px rgba(0,0.45,0,0.35);
    cursor: pointer;
}
.input-genre{
    transition: 1s;
    min-width: 150px;
background-color: #efb153;
box-shadow: -5px 10px 10px 5px rgba(0,0.45,0,0.35);
&:hover{
    transition: 1s;
    border: solid red;
}
}
.reset-filter{
    position: absolute;
    left: -55px;
    background-color: #ff6b01;
    box-shadow: -5px 10px 10px 5px rgba(0,0.45,0,0.35);
    cursor: pointer;
}
.genres-list{
    width: 75%;
    background-color: #f6ad54;
    color: #1d1d1c;
    border: black solid 2px;
    box-shadow: inset;
    list-style:none;
    opacity: 0.9;
    position: absolute;
    display: none;
}
.genres-list li{
    text-decoration: underline;
    font-weight: 700;
    &:hover{
        cursor:pointer;
        color: #f8f8f8;
    }
}